import React from "react"
import PropTypes from "prop-types"

import IMG_MARKET from '../images/market.svg';
import '../globals'
import Web3Modal from "web3modal";
import { StaticQuery, graphql, Link } from "gatsby"
import WalletConnectProvider from "@walletconnect/web3-provider";
import CONTRACT_DATA_NFT from '../contracts/NFT.json';
import CONTRACT_DATA_MARKET from '../contracts/NFTMarket.json';
import Hourglass from "./hourglass"
import Header from "./header"
import Modal from "./modal"
import "./layout.css"


const ADDRESS_NFT = "0x91BD719a249F6AAFDE5850392e1229e962bf3Df0";
const ABI_NFT = CONTRACT_DATA_NFT.abi;
const ADDRESS_MARKET = "0x22bF9B8f4568be87b0557c30505fCba698B06367";
const ABI_MARKET = CONTRACT_DATA_MARKET.abi;

const SYMBOL = "ONE";
const PROVIDER_RPC = {1666600000: "https://api.s0.t.hmny.io"};
const PROVIDER_NETWORK = "Harmony Mainnet";
const PROVIDER_CHAIN_ID = 1666600000;
const CHAIN_WRONG_ID = `Please switch to Harmony mainnet`;

var web3Modal = null;
var web3 = null;
const providerOptions = {
    walletconnect: {
        package: WalletConnectProvider, // required
        options: {
            rpc : PROVIDER_RPC,
            network : PROVIDER_NETWORK,
            chainId : PROVIDER_CHAIN_ID,
            infuraId: "INFURA_ID" // required
        }
    }
};

const Toast = ( props ) => { return <div className="toast"><strong>{ props.str }</strong></div> };

const Nft = ( props ) => {
    if ( props.token === undefined ) return <div className="nft">None</div>
        else return  <div className="nft"
    onClick={ e => props.eventHandler() }
    title={ props.token.name } style={ props.style ? props.style : {} }  >
        <img src={props.token.image} alt={ props.token.description} />
        </div>
};

class NftView extends React.Component {
    constructor( props ) {
        super( props );
        this.state = {
            more:false,
            showSellForm: false,
            approved: false,
            price: (this.props.marketInfo ? this.props.marketInfo.price : 0)
        }
    }

    componentDidMount(){
        if ( ! this.props.marketInfo ) {
            this.props.getApprovedNFT( this.props.token.id ).then( r => {
                this.setState({ approved: r } );
            }).catch( e => {
                this.setState({ approved: false } );
                console.log( e);
            });
        }
    }

    //   approveNFT={ this.marketApproveNFT }
    //      getApprovedNFT={ this.callMarketApprovedNFT }
    //    sellNFT={ this.marketSellNFT }
    //  buyNFT={ this.marketBuyNFT }


    render(){

        if ( this.props.token === undefined ) return <div className="nft">None</div>
            else return  <div title={ this.props.token.name } style={ this.props.style ? this.props.style : {} }  >
            <img src={this.props.token.image} alt={ this.props.token.description}
        style={{
            width: `67%`,
                margin: `1em auto`,
                display: `block`,
                border: `0.5em solid var(--main)`,
                borderRadius: `1em` }}/>
            { this.state.more ?

                <>   { ( !this.props.marketInfo ) ?  ( !this.state.showSellForm ? <button style={{borderRadius:`2em`,margin:`0.5em`,boxShadow: `0 0 4px 1px  var(--dark)`,borderColor: `var(--emphasis)`}} onClick={ e => this.setState({ showSellForm : true }) }><img src={IMG_MARKET} style={{height:`1rem`}} /> </ button> :
                    <div style={{ margin:`1em`,textAlign:`center`}}><button disabled={ this.state.approved } onClick={ e => {
                        this.props.approveNFT( this.props.token.id ).then( r => this.setState({ approved : r }) ).catch( e => console.error(e) )
                    } } type="button" className="action" style={{width:`30%`}}>Approve</button><input type="number" disabled={ !this.state.approved } onBlur={ e => this.setState({ price : e.target.value }) } placeholder="price" style={{width:`30%`}}/><button disabled={ !this.state.approved && this.state.price <= 0 } onClick={ e => {
                        this.props.sellNFT( this.props.token.id , web3.utils.toWei( this.state.price, "ether" ) );
                    } } className="action" type="button" style={{width:`30%`}} >sell</button></div> ) :  null }

                <textarea readOnly={true} value={ JSON.stringify( this.props.token) }
                style={{
                    overflow: `auto`,
                        width: `90%`,
                        margin: `auto`,
                        display: `block`,
                        borderRadius: `1em`,
                        height: `5em`
                }}

                ></textarea></>  :
                ( this.props.marketInfo ? <div style={{margin:`auto`,display:`block`,textAlign:`center`}} ><button type="button" className="action" style={{ background:`var(--light)`,borderColor:`var(--alert)` }} onClick={ e => this.props.buyNFT( this.props.marketInfo.itemId , this.props.marketInfo.price ) } >BUY</button><span style={{margin:`1em`}}>{web3.utils.fromWei( this.props.marketInfo.price, "ether" ) }<em>{SYMBOL}</em></span></div> : <button type="button" className="action" onClick={ e => this.setState({more:true}) }>. . .More</button> ) }
            </div>

    }

}



