//const TitledLayout = ({ title, children, vars, toast, connected, eventHandler }) => {
class TitledLayout extends React.Component {

    constructor( props ) {
        super( props );
        this.state = {
            connected: false,
            toast:null,
            myNFTs:[],
            marketItems:[],
            marketActivity:{},
            nfts: {},
            view:null
        }
    }

    eventHandler = ( what , data ) =>  {
        if ( null == what ) this.setState({"view":null});
        switch (what) {
            case "connect": {
                if ( !this.state.connected ) this.connect();
                else this.disconnect();
            } break;
            case "nft": {
                this.setState({"view":data});
            } break;
            case "context": {
                if ( this.state.context === "market" ) {
                    if ( this.state.myNFTs.length === 0 ) this.toast("First buy some NFTs");
                    else this.setState({ context : "wallet" });
                } else this.setState({ context : "market" });
            }
            default: {} break;
        }
    }

    componentDidMount() {
    }


    callFetchMarketItems = ( ) => {
        const anAddress = this.state.address;
        console.log(`callMarketItems(  )`);
        this.contractMarket.methods.fetchMarketItems( ).call().then( (r) => {
            console.log( r );
            for ( let i = 0; i < r.length; i++ ){

                this.setState({ marketItems: [ ...this.state.marketItems,
                    {
                        itemId: r[i]["itemId"],
                        nftContract: r[i]["nftContract"],
                        owner:r[i]["owner"],
                        price:r[i]["price"],
                        seller:r[i]["seller"],
                        sold:r[i]["sold"],
                        tokenId:r[i]["tokenId"]
                    }
                ]
                });

                if ( ! (r[i]["tokenId"] in this.state.nfts) ) {
                    this.contractNFT.methods.tokenURI( r[i]["tokenId"] ).call().then( d =>{
                        let _d = this.state.nfts;
                        if ( ! ( r[i]["tokenId"] in _d ) ) {
                            _d[ r[i]["tokenId"] ] = (d.startsWith("data") ? this.dataURLDecode( d ) : d);
                            this.setState({ nfts : _d });
                        }
                        return;
                    }).catch(e => this.errorHandler(e));
                }
            }
        }).catch( e => this.errorHandler(e));
    }

    initContractMarket = () => {
        try{
            console.log(`initContractNFT()`);
            this.contractMarket = new web3.eth.Contract( ABI_MARKET , ADDRESS_MARKET );
            this.setState({
                marketItems : [],
                marketActivity:[] }, this.callFetchMarketItems() );
        } catch(e) {
            this.errorHandler( e , "error in initialising Market contract");
        }
    }

    callWalletOfOwner = ( ) => {
        const anAddress = this.state.address;
        console.log(`callWalletOfOwner( ${ this.state.address } )`);
        this.contractNFT.methods.walletOfOwner( anAddress ).call().then( (r) => {
            console.log( r );
            Promise.all( r.map( async (i) => {
                await this.contractNFT.methods.tokenURI( i ).call().then( d =>{
                    let _d = this.state.nfts;
                    if ( ! ( i in _d ) ) {
                        _d[ i ] = (d.startsWith("data") ? this.dataURLDecode( d ) : d);
                        this.setState({ nfts : _d });
                    }
                    return;
                }).catch(e => this.errorHandler(e));
            }) ).then( () =>  {
                console.log( r );
                this.setState({ myNFTs: r });
            } );
        }).catch( e => this.errorHandler(e));
    }

    initContractNFT = () => {
        try{
            console.log(`initContractNFT()`);
            this.contractNFT = new web3.eth.Contract( ABI_NFT , ADDRESS_NFT );
            this.setState({
                myNFTs : [],
                nfts:{},
                marketItems:[],
                marketNFTs: [] }, this.callWalletOfOwner() );
        } catch(e) {
            this.errorHandler( e , "error in initialising NFT contract");
        }
    }


    dataURLDecode = ( url ) => {
        try{
            let tmp = url.split(',');
            let type = tmp[0].split(":")[1];
            if (type === "application/json;base64" ){
                let dataStr = atob(tmp[1]);
                return JSON.parse ( dataStr );
            };
        } catch (err) {
            console.error(err);
        }
        return "";
    };

    callMarketApprovedNFT = ( id ) => {
        return new Promise( (resolve, reject) => this.contractNFT.methods.getApproved( id ).call().then( r => {
            resolve( ADDRESS_MARKET === r );
        }).catch( e => {
            reject( false );
        }) );
    }

    marketApproveNFT = ( id ) => {
        return new Promise( (resolve, reject) => this.contractNFT.methods.approve( ADDRESS_MARKET , id ).send({ from: this.state.address }).then( r => {
            resolve( true );
        }).catch( e => {
            reject( false );
        }) );
    }

    marketSellNFT = ( id , wei) => {
        console.log( `marketSellNFT( ${ ADDRESS_NFT } , ${ id } , ${ wei } )`);
        this.contractMarket.methods.createMarketItem( ADDRESS_NFT , id , wei ).send({ from: this.state.address }).then( r => {
            if ( r?.events?.MarketItemCreated ){
                let i = r.events.MarketItemCreated.returnValues;
                let tkn = i.tokenId;
                this.setState({ marketItems: [ ... this.state.marketItems,
                    {
                        itemId: i.itemId,
                        nftContract:i.nftContract,
                        owner:i.owner,
                        price:i.price,
                        seller:i.seller,
                        sold:i.sold,
                        tokenId:i.tokenId
                    }
                ],
                    myNFTs: this.state.myNFTs.filter( i => tkn != i )
                });



            }

        }).catch( e => {
            console.error( e );
        });

    }

    marketBuyNFT = ( id , wei ) => {
        console.log( `marketBuyNFT( i${ ADDRESS_NFT } , ${ id } )`);
        this.contractMarket.methods.createMarketSale( ADDRESS_NFT , id ).send({ from: this.state.address, value: wei }).then( r => {
            if ( r.hasOwnProperty("transactionHash") ) {
                let tkn = this.state.marketItems.filter( i => id == i["itemId"] )[0]["tokenId"];
                this.setState({
                    myNFTs: [ ... this.state.myNFTs , tkn ],
                    marketItems: this.state.marketItems.filter( i => id != i["itemId"] ),
                });
            }
        }).catch( e => {
            console.error( e );
        });
    }

    errorHandler = ( e , s ) => {
        console.log(e);
        if (s) this.toast( s );
    }

    toast = ( s , t=5000 ) => {
        this.setState({ toast: s });
        setTimeout( () => this.setState({ toast: null }), t );
    }

    disconnect = () => { web3Modal.clearCachedProvider(); this.setState({connected : false }) };

    connect = async () => {

        console.log(`connection()`);

        try {

            web3Modal = new Web3Modal({
                cacheProvider: false, // optional
                providerOptions
            });
            const provider = await web3Modal.connect();

            const Web3 = require('web3');
            ////////////////////////////subscribe
            provider.on("accountsChanged", (accounts ) => {
                if ( parseInt( provider.chainId , 16 ) !== PROVIDER_CHAIN_ID ) {
                    this.toast( CHAIN_WRONG_ID , 8000 );
                    console.log(` accountsChanged event ${ provider.chainId } ${PROVIDER_CHAIN_ID }  `);
                    this.disconnect();
                } else {

                    this.toast( ("ACCOUNTS_CHANGED") );
                    const addr = accounts[0];
                    if ( addr ) {
                        this.setState({ address : addr , connected:true });
                        this.initContractNFT( );
                        this.initContractMarket();
                    }
                }
            })
            provider.on("chainChanged", (chainId ) => {
                if ( parseInt( chainId , 16 ) !== PROVIDER_CHAIN_ID ) {
                    console.log(` accountsChanged event ${ chainId }  ${ PROVIDER_CHAIN_ID  }`);
                    this.toast( CHAIN_WRONG_ID , 8000 );
                    this.disconnect();
                } else {
                    web3.eth.requestAccounts().then( (a) => {
                        const addr = a[0];
                        if ( addr ) {
                            this.setState({ address : addr ,connected:true });
                            this.initContractNFT( );
                            this.initContractMarket();
                        }
                    })
                }
            })
            provider.on("connect", (info) => {
                if ( parseInt( info.chainId , 16 ) !== PROVIDER_CHAIN_ID ) {
                    this.toast( CHAIN_WRONG_ID , 8000 );
                    console.log(`connect event ${ info.chainId }  ${ PROVIDER_CHAIN_ID  }`);


                } else {
                    web3.eth.requestAccounts().then( (a) => {
                        const addr = a[0];
                        if ( addr ) {
                            this.setState({ address : addr , connected:true });
                            this.initContractNFT( );
                            this.initContractMarket();
                        }
                    });
                }
            })
            provider.on("disconnect", (error) => {
                console.log(error);
                this.disconnect();
            });

            ////////////////////////////////////
            try{
                web3 = new Web3(provider);

            } catch (err) {
                console.error( err);
            }


            console.log(`connnect() => ${ parseInt( provider.chainId , 16 )} ${PROVIDER_CHAIN_ID}`);
            if ( parseInt( provider.chainId , 16 ) !== PROVIDER_CHAIN_ID ) this.toast(('CHAIN_WRONG_ID') );
            else {
                const accounts = await web3.eth.requestAccounts();
                const addr = accounts[0];
                if ( addr ) {
                    this.setState({ address : addr , connected:true });
                    this.initContractNFT(  );
                    this.initContractMarket();
                }
            }

        } catch( e ) {
            this.errorHandler( e , ('WALLET_ERROR') );
            this.disconnect();
            console.log( e );
        }
    }

    MyNFTsView = () => {
        return <> { this.state.myNFTs.map( (i,k) => <Nft key={k} token={ this.state.nfts[ i ] }
            eventHandler={ this.eventHandler.bind(null, "nft", this.state.nfts[ i ] ) } /> ) } </>
    }

    MarketNFTsView = () => {
        return <> { this.state.marketItems.map( (i,k) => <Nft key={k} token={ this.state.nfts[ i["tokenId"] ] }
            eventHandler={ this.eventHandler.bind(null, "nft", this.state.nfts[ i["tokenId"] ] ) } /> ) } </>
    }



    render(){
        return (
            <>
            <Header siteTitle={ this.props.title || `Title`} context={this.state.context} isConnected={this.state.connected} address={ this.state.context === "wallet" ? ADDRESS_NFT : ADDRESS_MARKET } eventHandler={this.eventHandler} />
            <div style={{
                margin: `0 auto`,
                    maxWidth: 1080,
                    padding: `0 1.0875rem 1.45rem`,
            }} >
            { this.state.view != null && <Modal
                evnt={ this.eventHandler} >
                { ( "image" in this.state.view ) ?
                    <NftView token={ this.state.view }
                    marketInfo={ ( this.state.myNFTs.includes( this.state.view.id) ) ? null  : this.state.marketItems.filter( (i) => i["tokenId"] == this.state.view.id )[0]  }
                    approveNFT={ this.marketApproveNFT }
                    getApprovedNFT={ this.callMarketApprovedNFT }
                    sellNFT={ this.marketSellNFT }
                    buyNFT={ this.marketBuyNFT }
                    eventHandler={ this.eventHandler }
                    /> :
                    null } </Modal> }
            { (this.state.toast !== null) && <Toast str={this.state.toast} /> }
            { ( this.state.connected ) ?  ( ( this.state.context == "market" || ( this.state.myNFTs && this.state.myNFTs.length == 0 ) ) ? ( this.state.marketItems.length > 0 ? this.MarketNFTsView() : <Hourglass /> ) : this.MyNFTsView() )
                    : <><main>{this.props.children}</main><footer>
                    <em>&copy; <Link to="/christian-lacdael" style={{color:`var(--main)`,textDecoration:`revert`}}>Christian Lacdael</Link> { (new Date()).getFullYear() }</em>
                    </footer>
                    </> }
            </div>
            </>
        )
    }
}


/*
  { connected && <> ( vars.myNFTS.map( (v,i) => {

          <Nft key={v} token={ vars.nfts[v] } />

      } ) ) </> }
      */

TitledLayout.propTypes = {
    children: PropTypes.node.isRequired,
}

const Layout = ( props ) => {
    return(
        <StaticQuery
        query={graphql`
        query SiteTitleQuery {
      site {
        siteMetadata {
          title
        }
      }
    }
  `} render={ data => <TitledLayout title={data.site.siteMetadata.title} children={props.children} /> }
        />);
}

export default Layout
