import { Link } from "gatsby"
import PropTypes from "prop-types"
import React from "react"
import IMG_LEAVE from '../images/leave.png';
import IMG_WALLET from '../images/wallet.svg';
import IMG_MARKET from '../images/market.svg';

//const quote = "A legend's path to guide your own";
const quote = "The restless spirits long to guide";
const Header = ({ siteTitle, context, address, isConnected, eventHandler }) => (
    <>
    <div style={{maxWidth:`1080px`,textAlign:`center`,margin:`auto`,position:`relative`}}>
    { (!isConnected) && <em id='quote'>{quote}</em> }

    <button style={{position:`absolute`,top:`0`,right:`0`,fontWeight:`bolder`,borderRadius:`2em`,margin:`0.5em`,boxShadow: `0 0 4px 1px  var(--dark)`,borderColor: `var(--emphasis)`

    }}
    type="button"
    onClick={ e => eventHandler( "connect" ,{} ) }
    >{ isConnected ? < img src={IMG_LEAVE} style={{height:`1rem`}} /> : 'Connect' }</button>


    { (isConnected) && <>
        <button style={{borderRadius:`2em`,margin:`0.5em`,boxShadow: `0 0 4px 1px  var(--dark)`,borderColor: `var(--emphasis)`}}
            onClick={ e => eventHandler( "context" ,{} ) } >
                <img src={ context === "market" ? IMG_WALLET : IMG_MARKET } style={{height:`1rem`}} /> </ button>
        <button style={{borderRadius:`2em`,margin:`0.5em`,boxShadow: `0 0 4px 1px  var(--dark)`,borderColor: `var(--emphasis)`}}
        type="button"
        ><span style={{
            maxWidth: `8em`,
                overflow: `hidden`,
                textOverflow: `ellipsis`,
                display: `block`,
                whiteSpace: `nowrap`
        }}>{ address }</span></button></>}





    </div>
    { (!isConnected) &&
        <nav>
        <div style={{
            margin: `0 auto`,
                maxWidth: 1080,
        }}>
        <ul>
        <li><Link to="/books">Books</Link></li>
        <li><Link to="/excerpts">Excerpts</Link></li>
        <li><Link to="/blog">Blog</Link></li>
        <li><Link to="/contact">Contact</Link></li>
        </ul>
        </div>
        </nav> }
    </>
)

Header.propTypes = {
    siteTitle: PropTypes.string,
}

Header.defaultProps = {
    siteTitle: ``,
}

export default Header
